.c-card {
  background-color: $background;
  display: flex;
  flex-direction: column;
}

.c-card__info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  line-height: 1;
}

.c-card__title {
  font-size: rem(24);
  text-align: center;
}

.c-card__category {
  padding: rem(8);
  background-color: $text-color2;
  font-size: rem(12);
}

.c-card__time {
  font-size: rem(12);
}

.c-card__body {
  padding: rem(16);
}

.c-card__info {
  padding: 0 rem(16);
  margin-top: auto;
}

.c-card__img {
  padding-top: 61.8046%;
  position: relative;
  min-width: rem(280);
}
.wp-block-image {
  padding-top: 61.8046%;
  position: relative;
}
.wp-block-image img {
  border-radius: rem(10);
  position: absolute;
  // top: 50%;
  // left: 50%;
  // transform: translate(-50%,-50%);
  top: 0;
  left: 0;
  // transform: translate(-50%,-50%);
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.c-card__img img {
  border-radius: rem(10);
  position: absolute;
  // top: 50%;
  // left: 50%;
  // transform: translate(-50%,-50%);
  top: 0;
  left: 0;
  // transform: translate(-50%,-50%);
  width: 100%;
  height: 100%;
  min-width: rem(280);
  object-fit: cover;
}

.c-card__text {
  padding-top: rem(16);
}

.c-card__price {
  text-align: right;
}
